import { FETCH_POSTS, NEW_POST } from "../actions/types";

const initialState = { 
    items: [], 
    item: {}
}

export default function(state = initialState, action){
    switch(action.type){
        case FETCH_POSTS:
            console.log("PostReducer->Fetch_Posts");
            return {
                ...state,
                items: action.payload
            };
        case NEW_POST:
            console.log("PostReducer->New_Post");
            return {
                ...state,
                item: action.payload
            };
        default:
            return state;
    }
}


